import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/Home.vue'
import program from '../views/Program.vue'
import registration from '../views/Registration.vue'
import contact from '../views/Contact.vue'
import links from '../views/Links.vue'
import diadiem from '../views/Diadiem.vue'
import thongbao from '../views/Thongbao.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/program',
    name: 'program',
    component: program
  },
  {
    path: '/registration',
    name: 'registration',
    component: registration
  },
  {
    path: '/links',
    name: 'links',
    component: links
  },
  {
    path: '/thongbao',
    name: 'thongbao',
    component: thongbao
  },
  {
    path: '/diadiem',
    name: 'diadiem',
    component: diadiem
  },
  {
    path: '/contact',
    name: 'contact',
    component: contact
  }
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
